import http from '@/api/http.js';
import QSPopup from '@/components/QS-popup/QS-popup.vue';
import mpvueCityPicker from '@/components/m-citypicker/mpvueCityPicker.vue';
import provinceDatas from '@/components/m-citypicker/city-data/province.js';
import cityData from '@/components/m-citypicker/city-data/city.js';
import areaData from '@/components/m-citypicker/city-data/area.js';
export default{
	components:{
		mpvueCityPicker,QSPopup
	},
	data() {
		return {
			isshowdistpicker:false,
			cityPickerValuea:'',
			isDefault:false,
			cityPickerValue: [0, 0, 1],
			themeColor: '#007AFF',
			region:{label:"请点击选择地址",value:[],cityCode:""},
			shouhren:'',
			phone:'',
			// companyname:'',
			xiangxidizhi:'',
			fault:2,
			cityssq:[],
			accode:'',
			id:0,
			province:'',
			city:'',
			area:'',
			member:'',
			tequan:'',
			changeType:0,
			group_buy_records_id:0,    //团长id
		};
	},
	onLoad(option){
		this.changeType = option.changeType
		this.id = option.id
		if(option.member){
			this.member = option.member
		}
		if(option.tequan){
			this.tequan = option.tequan
		}
		if(option.recordid){
			this.group_buy_records_id = option.recordid
		}
		this.getAddress()
	},
	// onNavigationBarButtonTap() {
	// 	this.show('QSPopup');
	// },
	methods:{
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref) {
			this.$refs[ref].hide();
		},
		hidedistpickerbox(){
			this.isshowdistpicker  = false;
		},
		getcity(cityobj){
			this.cityPickerValuea = cityobj.province.value+'/'+ cityobj.city.value+'/'+ cityobj.area.value;
		},
		toMine(){
			uni.redirectTo({
				url:'/pages/index/address/address?member=' + this.member + '&tequan=' + this.tequan + '&changeType=' + this.changeType + '&recordid='+this.group_buy_records_id
			})
		},
		del(){
			this.show('QSPopup');
		},
		delAddress(){
			let data = {
				token:uni.getStorageSync('token'),
				type:3,
				id:this.id
			}
			http('/api/User/act_address','post',data).then(res=>{
				if(res.data.status == '200'){
					this.hides('QSPopup')
					uni.navigateTo({
						url:'/pages/index/address/address'
					})
				}
			})
		},
		getAddress(){
			let data = {
				token:uni.getStorageSync('token'),
				address_id:this.id
			}
			http('/api/user/getUserAddress','post',data).then(res=>{
				if(res.data.status == '200'){
					this.shouhren = res.data.data.address.username;
					this.phone = res.data.data.address.phone;
					this.cityPickerValuea = res.data.data.address.address;
					this.province = res.data.data.address.province;
					this.city = res.data.data.address.city;
					this.area = res.data.data.address.area;
					this.fault=res.data.data.address.default;
					this.xiangxidizhi =res.data.data.address.detail_address;
					console.log(this.cityPickerValuea)
				}
				console.log(res)
			})
		},
		addmant(){
			let that = this;
			console.log('122121');
			try{
				if(!that.shouhren){
					throw '请填写收货地址人!';
				}
				if(!that.phone){
					throw '请填写电话号码!';
				}else{
					var reg =  /^1[3|4|5|7|8|9][0-9]{9}$/;
					if(!reg.test(that.phone)){
						throw '请填写正确的手机号码！';
					}
				}
				// if(!that.companyname){
				// 	throw '请填写企业名称!';
				// }
				if(!that.cityPickerValuea){
					throw '请选择省市区!';
				}
				if(!that.xiangxidizhi){
					throw '请填写详细收货地址!';
				}
				
			}catch(e){
				//TODO handle the exception
				that.$util.showToast(e);
				return false;
			}
			// let token = that.$util.getStorage('token');
			// let uid = this.$util.getStorage('uid');
			that.$util.showLoading('请稍后...');
			let data = {
				token:uni.getStorageSync('token'),
				type:2,
				id:that.id,
				username:that.shouhren,
				phone:that.phone,
				address:that.cityPickerValuea,
				detail_address:that.xiangxidizhi,
				default:that.fault,
				province:that.province,
				city:that.city,
				area:that.area
			}
			http('/api/User/act_address','post',data).then(res=>{
				if(res.data.status == '200'){
					that.$util.hideLoading()
					that.shouhren = '';
					that.phone = '';
					that.cityPickerValuea = '';
					that.fault= 1;
					that.xiangxidizhi ='';
					that.show('QSPopupSuccess')
					// uni.showModal({
					// 	title:'提示',
					// 	content:'编辑成功,是否返回地址管理?',
					// 	success(e) {
					// 		console.log(e);
					// 		if(e.confirm){
					// 			uni.navigateTo({
					// 				url:'/pages/index/address/address?member=' + that.member + '&tequan=' + that.tequan + '&changeType=' + that.changeType + '&recordid='+that.group_buy_records_id
					// 			})
					// 		}else{
					// 			that.shouhren = "";
					// 			that.phone= "";
					// 			that.xiangxidizhi= "";
					// 			that.fault= 0;
					// 			that.cityssq=[];
					// 			that.accode = '';
					// 		}
					// 	}
					// })
				}else{
					that.$util.showToast(res.data.msg);
				}
				console.log(res)
			})
			
		},
		addAddress(){
			uni.navigateTo({
				url:'/pages/index/address/address?member=' + this.member + '&tequan=' + this.tequan + '&changeType=' + this.changeType + '&recordid='+this.group_buy_records_id
			})
		},
		switchChange(e){
			console.log(e);
			this.fault  = e.detail.value?'1':'2';
		},
		onCancel(e) {
			console.log(e)
		},
		chooseCity() {



			this.$refs.mpvueCityPicker.show()
			// this.isshowdistpicker = true;
		},
		onConfirm(e) {
			let s = [];
			this.region = e;
			console.log(e.label);
			s = e.value;
			this.accode = provinceDatas[s[0]].value+'/'+cityData[s[0]][s[1]].value+'/'+areaData[s[0]][s[1]][s[2]].value;
			this.cityPickerValuea = e.label;
			this.cityssq = this.cityPickerValuea.split('/');
			this.province = this.cityssq[0]
			this.city = this.cityssq[1]
			this.area = this.cityssq[2]
		}
	}
}
